<template>
  <div class="personal-content-body">
    <div class="content">
      <div class="content-title clear">
        <!-- <div class="left">机构公告</div> -->
        <!-- <div class="search-box right">
                <input class="search-box-input" type="text" placeholder="搜索" @change="keyWordChange" v-model="keyWord">
                <span class="search-box-icon icon-search"></span>
            </div>
            <el-date-picker v-model="selectTime" type="date" placeholder="选择日期" :clearable='false' value-format="yyyy-MM-dd" @change="toselectTime" class="header-right-time right">
            </el-date-picker> -->
      </div>
      <div class="list-contaienr">
        <div class="list-header" v-if="params.type==0">共{{params.total}}篇机构公告（{{params.unreadTotal}}篇公告未读)</div>
        <div class="list-header" v-if="params.type==1">共{{params.total}}篇企业私信（{{params.unreadTotal}}篇私信未读)</div>
        <div class="list-nav">
          <span v-if="params.type==0" @click="goBack">机构公告&nbsp;&nbsp;></span>
          <span v-if="params.type==1" @click="goBack">企业私信&nbsp;&nbsp;></span>
          <!-- <span></span> -->
          <span>文章详情页</span>
        </div>
        <div class="list-line"></div>
        <div class="list-body">
          <div class="article-name">{{detailsInfo.title}}</div>
          <div class="article-publish-time">发布时间：{{detailsInfo.issue_time}} 来源：{{detailsInfo.source}}</div>
          <div>
            <div class="article-title-icon"></div>
          </div>
          <div class="article-content" v-html="detailsInfo.contents">
            <!-- 题主说，觉得做一个又浪又认真的码农其实挺难。但据我们观察，并没有题主想的那么难。 我们在知乎、100offer、微信社群等多个平台上接触的程序员还是有一些又浪又认真的姿势。为了便于理解，我们按照时间线把我们接触下来的多种程序员生活姿势进行了梳理。 7 AM-9 AM 上班前的宝贵时光一些没有被加班困扰，或者习惯早起的程序员，一般这个时候就会起床。 晨跑、读一些比较难啃专业书籍成为这个时间段能做的主要两件事。如果住得远，可能还得早点出门。毕竟只有单身的程序员能够租个离公司近的房子。 12 AM 午休中午不睡，下午崩溃，何况对于大部分程序员下班的时候已经能看星星了。所有中午午休很重要。常见的除了午睡、看比赛之外，还会饭后溜达溜达。（对，其实他们没有你想的这么宅，毕竟他们一天能够晒到太阳的时间并不多。午休抓紧时间晒晒太阳，和同事溜达溜达，讨论一下工作中遇到的bug。
                    7PM 第一批程序员下班嗯，一般这个点下班的程序员输于程序员群体中比较拉仇恨的，在互联网公司中并不多见。反而一般碰到这样的同僚，还会揶揄几句，你们公司是要倒闭了吗？如果这个公司并没有药丸，业务发展的也还不错，可能你的程序员小伙伴，就会来找你内推了。 8 PM 第二批程序员下班8点则是比较常见的下班时间。毕竟粗略算算也在公司搬了块10个小时砖了。 这两批程序员下班后可供支配的时间较多，至少有3-4个小时。这批程序员通常能充分利用下班时间，一般会有以下几种选择：贤惠路线：去超市买菜、做明天一天的饭（贴心提示，想要找程序员男朋友的妹子把这个考察点列入
                    list）游戏狂热型：讲好了一起开黑，约好一起吃鸡，还有各种打比赛的，五花八门。不要羡慕，这一般都是没有女朋友也没有结婚的，俗称三无程序员（无对象、无房、无猫）。健身达人型：每天都要去健身房，或者公司就有健身房。8 块腹肌才是本体，开健身工作室才是人生目标。工作狂型：工作时间还不能满足我，我需要复盘，我需要写 Review，我需要进步。刻苦提升型：总有想要不停点亮的技能点，总觉得不做点什么新东西就浑身不舒服的工程师。可以和公开课渡过数千个日日夜夜，即使奶娃，也要先去看看最权威的paper怎么说。
                    10 PM以后 加班狗程序员下班拖着疲惫的身躯，带着被工作消耗过大的大脑，坐上回家的taxi。 这一批加班重灾区的程序员通常没有什么晚上的自由支配时间。回家就洗洗睡了。除了一些晚上下班可以进行的活动的拓展，周末和假期也是程序员抓住机会浪的时间，他们通常有如下选择：兴趣实践型：工作之外和代码绝缘，弹吉他、做木工等等，具体可以参见：100offer的提问：你认识的程序员除了敲代码，还有哪些隐藏的神技能？社交型：最近加的XXX群有面基，去去去。最近有个技术沙龙，去去去。XXX
                    在的那个公司最近好火，去 social，吃个饭聊聊。户外运动爱好者：从徒步、爬山、骑车到有些竞技性的项目：马拉松等等。这个时候，可以名正言顺地穿起最爱的冲锋衣了。沉迷于个人项目无法自拔：除了自己造轮子，还喜欢各种小硬件。有时还能拿自己的项目去参加个什么比赛，拿到一笔资金接着升级。梳理下来，其实程序员和从事其他行业、职位的同学在生活上真的没有你想的这么大的区别。可能真的加班会比较多，但是并不影响他们「又浪又认真」。所以请不要脑补「被嫌弃的程序员的一生」或者「被加班支配的程序员的一生」了。 -->
            <!-- {{detailsInfo.contents}} -->
          </div>
        </div>
        <div class="list-footer">
          <button @click="getArticleDetails(upId)">上一篇</button>
          <button @click="getArticleDetails(downId)">下一篇</button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {
  getInfoDetails,
} from '../../config/api'
// import router from '../../router/router';
export default {
  data() {
    return {
      detailsInfo: {},
      selectTime: null,
      keyWord: null,
      type: null,
      downId: null,
      upId: null,
      params: {},
    };
  },
  mounted() {
    this.params = this.$route.query;
    this.getArticleDetails(this.params.id);
  },
  methods: {
    //获取详情信息
    getArticleDetails(detailId) {
      if (detailId == null) {
        return false
      }
      let params = {
        id: detailId,
        type: this.params.type,
      }
      if (this.params.searchType == 'time') {
        params.issueTime = this.selectTime
      } else if (this.params.searchType == 'keyWord') {
        params.search = this.keyWord;
      }
      getInfoDetails(params).then(res => {
        if (res.data.msgNo == 1) {
          this.detailsInfo = res.data.payload.data;
          this.downId = res.data.payload.downId;
          this.upId = res.data.payload.upId
        }
      })
    },
    goBack() {
      if (this.params.type == 0) {
        this.$router.push({
          path: '/home/organizationNotice'
        })
      } else if (this.params.type == 1) {
        this.$router.push({
          path: '/home/organizationSx'
        })
      }
    }
  }
};

</script>
<style lang="scss" scoped>
.content {
  width: 100%;
  min-height: 100%;
  background-color: white;
  padding: 40px;
  padding-top: 30px;
}

.hidden {
  display: none;
}

.content-title {
  margin-bottom: 18px;
}

.content-title>div.left {
  font-size: 18px;
  line-height: 40px;
  color: #35383A;
  margin-bottom: 29px;
  font-weight: bold;
}

.header-right-time {
  // width: 115px;
  margin-right: 10px; // position: relative;
  // top: 4px;
}

.content-header button {
  border: 1px solid #f4f4f4;
  border-radius: 2px;
  padding: 1px 10px;
  font-size: 14px;
  color: #808080;
  outline: none;
  margin-left: 24px;
}

.content-header button:hover {
  border: 1px solid #f4f4f4;
  color: #4a90e2;
  background-color: rgba(#488cdc, 0.1);
  cursor: pointer;
}

.content-header button span {
  display: inline-block;
  width: 15px;
  height: 15px;
  position: relative;
  top: 2px;
  background-size: 15px;
}

.rl-btn {
  margin-right: 10px;
}

.list-contaienr {
  border: 1px solid #e4e4e4;
  padding: 20px 17px;
  position: relative;
}

.list-header {
  text-align: left;
  color: #3c98e8;
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 13px;
}

.list-nav {
  padding-left: 14px;
  margin-bottom: 12px;
}

.list-nav span {
  font-size: 14px;
  color: #35383A
}

.list-nav span:nth-child(1) {
  color: #3C98E8;
  cursor: pointer;
}

.list-line {
  border-bottom: 1px solid #f4f4f4;
}

.list-body {
  width: 100%;
  padding: 17px 13px;
  overflow: auto;
  height: calc(100% - 55px)
}

.list-body .article-name {
  text-align: center;
  font-size: 22px;
  color: #35383A;
  font-weight: bold;
}

.list-body .article-publish-time {
  text-align: center;
  font-size: 14px;
  color: #A0A4AA;
  margin-top: 6px
}

.list-body .article-content {
  font-size: 14px;
  color: #35383A;
  line-height: 24pt;
  margin-top: 8px;
}

.list-body .article-title-icon {
  display: inline-block;
  width: 20px;
  height: 4px;
  background-color: #3C98E8;
}

.list-footer {
  text-align: center;
  margin-top: 6px;
  margin-bottom: 30px;
}

.list-footer button {
  width: 74px;
  height: 28px;
  border: 1px solid #E3EAEF;
  color: #A0A4AA;
  font-size: 14px;
  margin: 0 6px;
}

.nocursor {
  cursor: none;
}

</style>



